@media (max-width: 900px) {
  .burgerBtn {
    z-index: 10;
    left: 5%;
    top: 5%;
    display: block;
    position: fixed;
    width: 35px;
    height: 21px;
    cursor: pointer;
    &::before, &::after, span {
      left: 0;
      position: absolute;
      height: 20%;
      width: 100%;
      transition: all 0.3s ease 0s;
      background-color: #fabd4e;
      border: 0.5px solid black;
    }
    &::before, &::after {
      content: "";
    }
    &::before {
      top: 0;
    }
    &::after {
      bottom: 0;
    }
    span {
      top: 50%;
      transform: scale(1) translate(0px, -50%);
    }
  }
  .active {
    & span {
      border: none;
      transform: scale(0) translate(0px, -50%);
    }
    &::before {
      top: 50%;
      border: none;
      transform: rotate(-45deg) translate(0, -75%);
    }
    &::after {
      bottom: 50%;
      border: none;
      transform: rotate(45deg) translate(0, 75%);
    }
  }
}